import { ismatrix, Matrix  } from 'mdatools/arrays';


function floor(x) {
   return Math.floor(x);
}

function get_mpl_baseline(nx, mp) {

   if (mp[0] === 0) {
      const p = mp[1] + 2;
      return 2 + p + nx * p + 2 * nx + p; // EMSC: type, p, A (nx x p), lnorm (nx), x_ref(nx), c (p)
   }

   // ALS: type, lambda, p and place for baseline
   return 3 + nx;
}

// !!! order should match the one in C !!!
export const methodsList = [
   {id: 'norm',      title: 'Normalization',    mpl: 2, get_mpl_new: (nx, mp) => 2,                   mp: [0, 0]},
   {id: 'savgol',    title: 'Savitzky-Golay',   mpl: 3, get_mpl_new: (nx, mp) => mp[0] * mp[0],       mp: [3, 1, 0]},
   {id: 'scale',     title: 'Scaling',          mpl: 2, get_mpl_new: (nx, mp) => 2 * nx,              mp: [1, 0]},
   {id: 'baseline',  title: 'Baseline',         mpl: 3, get_mpl_new: get_mpl_baseline,                mp: [0, 0, 0]},
   {id: 'trim',      title: 'Trim tails',       mpl: 2, get_mpl_new: (nx, mp) => 2,                   mp: [0, 0]},
   {id: 'spikes',    title: 'Spike removal',    mpl: 2, get_mpl_new: (nx, mp) => 2 + 3 * nx + mp[0],  mp: [5, 5]},
];


/**
 * Finds last trimming method in the model if any and use it to estimate left and right
 * trim values.
 *
 * @param {JSON} m - JSON with preprocessing model
 * @returns array with left and right values (or [0, 0])
 */
export function getTrim(m) {
   const trimID = methodsList.findIndex(v => v.id === 'trim');
   const pos = m.ml.findLastIndex(v => v === trimID);
   if (pos < 0) return [0, 0];
   const offset = m.mpl.slice(0, pos).reduce((acc, v) => acc + v, 0);
   return [m.mp[offset], m.mp[offset + 1]];
}



export function prep2str(m) {
   let out = '<ul>';
   for (let i = 0; i < m.ml.length; i++) {
      const name = methodsList[m.ml[i]].title;
      out = out + '<li>' + name + ' <span>' +  m.info[i] + '</span></li>';
   }
   out = out + '</ul>';
   return out;
}
/**
 * Creates and applies a preprocessing model.
 *
 * @param {Object} Module object with wasm module for PCV.
 * @param {Matrix} D matrix with training set (predictors and responses).
 * @param {number} nresp number of responses.
 * @param {Array} prepobj array with preprocessing methods (method ID and its user defined parameters).
 * @param {boolean} transposed logical value, if true data has been already transposed (objects are columns),
 *
 * @returns object with preprocessing model and matrix with preprocessed data.
 */
export function prep_fit(Module, D, nresp, prepobj, transposed = true) {

   if (!ismatrix(D)) {
      throw new Error('prep: D must be a matrix.');
   }

   if (!Array.isArray(prepobj) || prepobj.length < 1) {
      throw new Error('prep_fit: list with methods is not correct.');
   }

   let nrows, npred;
   if (transposed) {
      nrows = D.ncols;
      npred = D.nrows - nresp;
   } else {
      nrows = D.nrows;
      npred = D.ncols - nresp;
   }

   const nmethods = prepobj.length;
   if (nmethods < 1) {
      throw new Error('prep_fit: no preprocessing methods are provided.');
   }

   // compute length of vectors "mp" and "mp_new"
   // and set values for "ml", "mpl", and "mpl_new"

   const ml = new Int32Array(nmethods);
   const mpl = new Int32Array(nmethods);
   const mpl_new = new Int32Array(nmethods);

   let mp_length = 0;
   let mp_new_length = 0;

   for (let i = 0; i < prepobj.length; i++) {
      const id = prepobj[i].id;
      const params = prepobj[i].params;
      const ind = methodsList.findIndex(v => v.id == id);
      if (ind < 0) {
         throw new Error('prep_fit: wrong method name: ' + id + '.');
      }

      const method = methodsList[ind];
      if (params.length !== method.mpl) {
         throw new Error('prep_fit: wrong number of parameters for method: ' + id + '.');
      }

      ml[i] = ind;
      mpl[i] = method.mpl;
      mpl_new[i] = method.get_mpl_new(npred, params);
      mp_length = mp_length + mpl[i];
      mp_new_length = mp_new_length + mpl_new[i];
   }

   // set values for mp
   const mp = new Float64Array(mp_length);
   let offset = 0;
   for (let i = 0; i < prepobj.length; i++) {
      const id = prepobj[i].id;
      const params = prepobj[i].params;
      const ind = methodsList.findIndex(v => v.id == id);

      mp.set(params, offset);
      offset = offset + mpl[i];
   }


   // allocate memory in WASM heap space and copy original values
   const ds_ptr = Module._malloc(D.v.length * D.v.BYTES_PER_ELEMENT);
   Module.HEAPF64.set(D.v, ds_ptr / D.v.BYTES_PER_ELEMENT);

   // allocate memory in WASM heap space and values from methods list arrat
   const ml_ptr = Module._malloc(ml.length * ml.BYTES_PER_ELEMENT);
   Module.HEAP32.set(ml, ml_ptr / ml.BYTES_PER_ELEMENT);

   // allocate memory in WASM heap space and values from array with number of parameters for each method
   const mpl_ptr = Module._malloc(mpl.length * mpl.BYTES_PER_ELEMENT);
   Module.HEAP32.set(mpl, mpl_ptr / mpl.BYTES_PER_ELEMENT);

   // allocate memory in WASM heap space and values from array with number of adjusted parameters for each method
   const mpl_new_ptr = Module._malloc(mpl_new.length * mpl_new.BYTES_PER_ELEMENT);
   Module.HEAP32.set(mpl_new, mpl_new_ptr / mpl_new.BYTES_PER_ELEMENT);

   // allocate memory in WASM heap space and values from array with user defined parameters
   const mp_ptr = Module._malloc(mp.length * mp.BYTES_PER_ELEMENT);
   Module.HEAPF64.set(mp, mp_ptr / mp.BYTES_PER_ELEMENT);

   // allocate memory in WASM heap space and values from array with methods parameters to be computed inside
   const mp_new_ptr = Module._malloc(mp_new_length * mp.BYTES_PER_ELEMENT);

   // allocate memory in WASM heap space for preprocessed data
   const dp_ptr = Module._malloc(D.v.length * D.v.BYTES_PER_ELEMENT);

   // call WASM function for Preprocessing
   const res = Module.ccall(
      'prepfit_wasm',
      [ 'number'], // return type
      [ 'number', 'number', 'number', 'number', 'number', 'number', 'number', 'number', 'number', 'number', 'number', 'number'],
      [ds_ptr, nrows, npred, nresp, nmethods, ml_ptr, mpl_ptr, mp_ptr, mpl_new_ptr, mp_new_ptr, dp_ptr, transposed],
   );

   if (res !== 1) {
      throw new Error('prep_fit: unexpected error.');
   }

   // copy part of HEAP memory with preprocessed data
   const Dp = Module.HEAPF64.slice(
      dp_ptr / D.v.BYTES_PER_ELEMENT,
      dp_ptr / D.v.BYTES_PER_ELEMENT + D.v.length,
   );

   // copy part of HEAP memory with preprocessed data
   const mp_new_out = Module.HEAPF64.slice(
      mp_new_ptr / mp.BYTES_PER_ELEMENT,
      mp_new_ptr / mp.BYTES_PER_ELEMENT + mp_new_length,
   );

   // free the memory
   Module._free(ds_ptr);
   Module._free(ml_ptr);
   Module._free(mpl_ptr);
   Module._free(mp_ptr);
   Module._free(dp_ptr);

   // create preprocessing model object
   const model = {
      ml, mpl, mp, mpl_new, mp_new: mp_new_out, npred: npred, class: ['prepmodel']
   };

   // return preprocessing model and the preprocessed data values
   return {model: model, data: new Matrix(Dp, D.nrows, D.ncols)};
}


/**
 * Creates and applies a preprocessing model.
 *
 * @param {Object} Module object with wasm module for PCV.
 * @param {Matrix} D matrix with training set (predictors and responses).
 * @param {number} nresp number of responses.
 * @param {Object} model JSON with preprocessing model returned by 'prep_fit'.
 * @param {boolean} transposed logical value, if true data has been already transposed (objects are columns),
  *
 * @returns {Matrix} Matrix with preprocessed data.
 */
export function prep_apply(Module, D, nresp, model, transposed = true) {

   if (!ismatrix(D)) {
      throw new Error('prep_apply: D must be a matrix.');
   }

   let nrows, npred;
   if (transposed) {
      nrows = D.ncols;
      npred = D.nrows - nresp;
   } else {
      nrows = D.nrows;
      npred = D.ncols - nresp;
   }

   // check number of predictors
   if (npred !== model.npred) {
      throw new Error('prep_apply: D has wrong number of predictors (expected ' + model.npred + ' got ' + npred + ').');
   }

   // check the preprocessing model structure
   const ml = model.ml;
   const mpl_new = model.mpl_new;
   const mp_new = model.mp_new;
   const mp_new_length_exp = mpl_new.reduce((acc, v) => acc + v, 0);
   if (!ml || ml.length < 1 || !mpl_new || mpl_new.length !== ml.length || mp_new.length !== mp_new_length_exp) {
      throw new Error('prep_apply: wrong structure of the model object.');
   }

   const nmethods = ml.length;

   // allocate memory in WASM heap space and copy original values
   const ds_ptr = Module._malloc(D.v.length * D.v.BYTES_PER_ELEMENT);
   Module.HEAPF64.set(D.v, ds_ptr / D.v.BYTES_PER_ELEMENT);

   // allocate memory in WASM heap space and values from methods list arrat
   const ml_ptr = Module._malloc(ml.length * ml.BYTES_PER_ELEMENT);
   Module.HEAP32.set(ml, ml_ptr / ml.BYTES_PER_ELEMENT);

   // allocate memory in WASM heap space and values from array with number of parameters for each method
   const mpl_new_ptr = Module._malloc(mpl_new.length * mpl_new.BYTES_PER_ELEMENT);
   Module.HEAP32.set(mpl_new, mpl_new_ptr / mpl_new.BYTES_PER_ELEMENT);

   // allocate memory in WASM heap space and values from array with user defined parameters
   const mp_new_ptr = Module._malloc(mp_new.length * mp_new.BYTES_PER_ELEMENT);
   Module.HEAPF64.set(mp_new, mp_new_ptr / mp_new.BYTES_PER_ELEMENT);

   // allocate memory in WASM heap space for preprocessed data
   const dp_ptr = Module._malloc(D.v.length * D.v.BYTES_PER_ELEMENT);

   // call WASM function for Preprocessing
   const res = Module.ccall(
      'prepapply_wasm',
      [ 'number'], // return type
      [ 'number', 'number', 'number', 'number', 'number', 'number', 'number', 'number', 'number', 'number'],
      [ds_ptr, nrows, npred, nresp, nmethods, ml_ptr, mpl_new_ptr, mp_new_ptr, dp_ptr, transposed],
   );

   if (res !== 1) {
      throw new Error('prep_apply: unexpected error.');
   }

   // copy part of HEAP memory with preprocessed data
   const Dp = Module.HEAPF64.slice(
      dp_ptr / D.v.BYTES_PER_ELEMENT,
      dp_ptr / D.v.BYTES_PER_ELEMENT + D.v.length,
   );

   // free the memory
   Module._free(ds_ptr);
   Module._free(ml_ptr);
   Module._free(mpl_new_ptr);
   Module._free(mp_new_ptr);
   Module._free(dp_ptr);

   // return preprocessed data as a matrix
   return new Matrix(Dp, D.nrows, D.ncols);
}
