<script>
   /*************************************************************/
   /* ModelLoader                                               */
   /* -------------------------                                 */
   /* loads preprocessing model from file                       */
   /*************************************************************/

   import { FileSelector, ButtonCancel, Container, Button } from 'svelte-controls-basic';
   import { reviver } from '../../../shared/utils';
   import { prep2str } from '../../../shared/wasm/dist/prep';

   let {
      model = $bindable(),
      left = $bindable(),
      right = $bindable()
   } = $props();    // object with model


   // string with model file name
   let modelFile = $state('');

   // app status for handling and showing errors
   let status = $state('neutral');
   let errMsg = $state('');

   // gets model content as one large string, parse it into matrix
   function setModel(strData) {
      try{
         const m = JSON.parse(strData, reviver);
         if (Array.isArray(m.v)) {
            m.v = Float64Array.from(m.v);
         }
         if (!Object.keys(m).includes('class') || m.class[0] !== 'prepmodel') {
            throw Error('The JSON file you selected does not contain preprocessing model.');
         }
         m['fileName'] = modelFileName;
         model = m;
         status = 'success';
         errMsg = ''
      } catch (e) {
         console.log(e);
         model = null;
         status = 'error';
         errMsg = e;
      }
   }

   // promise for reading the JSON file
   function readFile (e) {
      const reader = new FileReader();
      reader.onload = () => { setModel(reader.result) };
      reader.readAsText(modelFile);
   }

   function reset() {
      modelFile = '';
      model = null;
      status = 'neutral';
      errMsg = '';
   }

   // reactive expression for tracking changes of 'modelFile'
   $effect(() => {
      if (modelFile === null) {
         // if model file is empty reset status and error messages
         model = null;
         status = 'neutral';
         errMsg = '';
      } else {
         // if not just remove error messages if any
         status = 'neutral';
         errMsg = '';
      }
   });

   // file name with model without extension
   let modelFileName =$derived(modelFile ? modelFile.name.replace(/\.[^/.]+$/, "") : '');

   let listHidden = $state(true);
   function toggleList() {
      listHidden = !listHidden;
   }

</script>

{#if !model }

   <!-- show if data is not loaded -->
   <!-- reset component if dataFile is changed inside -->
   {#key modelFile}
   <Container name="file-selector">
      <FileSelector id="modelFile" bind:file={modelFile} acceptType=".json" message="Select file with model"/>
   </Container>
   {/key}

   {#if modelFile}
   <Container name="load-button" label="&nbsp;">
      <Button onclick={readFile} text="Load" />
   </Container>
   {/if}

{:else}

   <!-- dataset is loaded - show info and cancel button -->
   <Container name="prep-info">
      <p class="info-text">
         <span style="display:inline-block;font-weight:bold;padding-bottom: 0.25em;">Preprocessing model</span><br>
         <span>file:</span> <span title={model.fileName} class="file-name">{model.fileName}</span><br>
         <span style="color:#a0a0a0;"># variables:</span> {model.npred}<br>
         <span style="color:#a0a0a0;"># methods:</span> {model.ml.length} <br>
         <span class="methods-list" class:hidden={listHidden}>
         {@html prep2str(model)}
         </span>
         <span class="span-button"><Button onclick={toggleList} text={listHidden ? "show" : "hide"} /></span>
      </p>
      <ButtonCancel onclick={reset} />
   </Container>

{/if}

<!-- error message -->
{#if status === 'error' || errMsg}
<Container name="error" status="error">
   <p>{@html errMsg}</p>
</Container>
{/if}


<style>
   :global(.prep-info) {
      position: relative;
   }

   .info-text {
      line-height: 1.5em;
   }

   .info-text > :global(span) {
      vertical-align:top;
      color: #a0a0a0;
   }

   .info-text > .file-name {
      display: inline-block;
      padding: 0;
      margin: 0;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      max-width: 25ch;
      color: #222;
   }

   .methods-list {
      box-sizing: border-box;
      /* position: absolute;
      left: 0;
      top: 0; */
      background: #fff;
      border-radius: 0.25em;
      width: 100%;
      z-index: 999;
   }

   .methods-list :global(span){
      display: inline-block;
      padding: 0.1em 0.5em;
      margin-left: 0.5em;
      background: #f0f0f0;
      border-radius: 3px;
   }

   .methods-list :global(ul){
      list-style-type: disc;
      padding: 0.25em 0.25em 0.25em 2.0em;
      margin: 0;
   }

   .methods-list :global(li){
      padding: 0.25em 0;
   }

   .hidden {
      display: none;
   }

   .span-button {
      display: flex;
      padding: 0.2em 0 0 0 ;
      justify-content: center;
   }
</style>